<template>
    <div>
      <div class="box" v-for="(elem,i) of list" :key="i">
         <img  :src="`http://106.12.179.113:3000/`+elem.img_url0" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url1" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url2" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url3" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url4" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url5" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url6" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url7" alt="">
         <img  :src="'http://106.12.179.113:3000/'+elem.img_url8" alt="">
      </div>
    </div>
</template>
<script>
  export default{
    data(){
      return {
          list:[]
      }
    },created(){
        if(this.list.length<1){
          this.loadfile()
        }
    },methods:{
        loadfile(){
          var url=`common/fenxiang`
          this.axios.get(url).then(result=>{
          result.data.result.forEach(element => {
                   this.list.push(element)
            });
                  this.$store.commit("img",this.list)
                   console.log(this.list)
            })
            }
    },
    mounted(){
      if(this.$store.state.img.length>0){
                this.list=this.$store.state.img
        }
    }
  }
</script>
<style lang="scss" scoped>
  .box{
    width:95%;
    margin:10px auto;
    border: 1px solid;
  }
  img{
    width: 100%;
  }
</style>